<script>
// import categories from '@/data/categories';
// import colors from '@/data/colors';
import axios from 'axios';
import API_BASE_URL from '@/config';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: 0,
      CategoriesData: null,
      ColorsData: null,
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorId'],
  computed: {
    categories() {
      return this.CategoriesData ? this.CategoriesData.items : [];
    },
    colors() {
      return this.ColorsData ? this.ColorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    colorId(value) {
      this.currentColor = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:colorId', this.currentColor);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:colorId', 0);
    },
    loadCategories() {
      axios.get(`${API_BASE_URL}/api/productCategories`).then((response) => { this.CategoriesData = response.data; });
    },
    loadColors() {
      axios.get(`${API_BASE_URL}/api/colors`).then((response) => { this.ColorsData = response.data; });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>
