import Vue from 'vue';
import Vuex from 'vuex';
// import products from '@/data/products';
import axios from 'axios';
import API_BASE_URL from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    // cartProducts: [{
    //   productId: 1,
    //   amount: 1,
    // }],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
    cartLoading: true,
    // cartLoadingFailed: false,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    // addProductToCart(state, {
    //   productId,
    //   amount,
    // }) {
    //   const items = state.cartProducts.find((item) => item.productId === productId);
    //   if (items) {
    //     items.amount += amount;
    //   } else {
    //     state.cartProducts.push({
    //       productId,
    //       amount,
    //     });
    //   }
    // },
    updateCartProductAmount(state, {
      productId,
      amount,
    }) {
      const its = state.cartProducts.find((item) => item.productId === productId);
      if (its) {
        its.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const {
          product,
        } = state.cartProductsData.find((p) => p.product.id === item.productId);
        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item
        .amount) + acc, 0);
    },
    cartTotalAmount(state) {
      return state.cartProducts.length;
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      this.state.cartLoading = true;
      // this.state.cartLoadingFailed = false;
      clearTimeout(this.loadCartTimer);
      this.loadCartTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((response) => {
            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccessKey', response.data.user.accessKey);
              context.commit('updateUserAccessKey', response.data.user.accessKey);
            }
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          })
        // .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.state.cartLoading = false; });
      }, 2000);
      // this.state.cartLoading = false;
    },
    addProductToCart(context, { productId, amount }) {
      this.state.cartLoading = true;
      return (new Promise((resolve) => { setTimeout(resolve, 5000); })).then(() => {
        axios.post(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        }).then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        }).then(() => { this.state.cartLoading = false; });
        // this.state.cartLoading = false;
      });
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });
      if (amount < 1) {
        return true;
      } return axios.put(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items);
      }).catch(() => {
        context.commit('syncCartProducts');
      });
    },
    deleteCartProduct(context, productId) {
      this.state.cartLoading = true;
      clearTimeout(this.deleteCartTimer);
      this.deleteCartTimer = setTimeout(() => {
        axios.delete(`${API_BASE_URL}/api/baskets/products`, {
          data: { productId },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        }).then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        }).then(() => { this.state.cartLoading = false; });
      }, 2000);
    },
  },
});
